"use client";

import { useState } from "react";
import Button from "@/components/ui/button/Button";
import Label from "@/components/form/Label";
import Input from "@/components/form/input/InputField";
import TextArea from "@/components/form/input/TextArea";

type Props = {
  onCreated?: (payload: { title: string; body: string }) => void;
};

export default function AdminAnnouncementComposer({ onCreated }: Props) {
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");

  const canPublish = body.trim().length > 0;

  const handlePublish = () => {
    if (!canPublish) return;

    // mock publish
    console.log("ADMIN_ANNOUNCEMENT_PUBLISH (mock):", {
      title: title.trim(),
      body: body.trim(),
    });

    onCreated?.({ title: title.trim(), body: body.trim() });

    // reset (mock)
    setTitle("");
    setBody("");
    alert("Anuncio publicado (mock)");
  };

  return (
    <div className="rounded-2xl border border-gray-200 bg-white p-5 dark:border-gray-800 dark:bg-white/[0.03] lg:p-6">
      <div className="flex items-start justify-between gap-4">
        <div>
          <h3 className="text-lg font-semibold text-gray-800 dark:text-white/90">
            Publicar anuncio
          </h3>
          <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
            Solo visible para Admin. Los atletas verán el anuncio en Comunidad y en la campanita.
          </p>
        </div>

        <Button variant="primary" disabled={!canPublish} onClick={handlePublish}>
          Publicar
        </Button>
      </div>

      <div className="mt-5 grid grid-cols-1 gap-4">
        <div>
          <Label>Título (opcional)</Label>
          <Input
            placeholder="Ej: Mañana el box está cerrado"
            defaultValue={title}
            onChange={(e) => setTitle((e?.target as HTMLInputElement)?.value ?? "")}
          />
        </div>

        <div>
          <Label>Mensaje</Label>
          <TextArea
            rows={5}
            placeholder="Escribí el anuncio…"
            defaultValue={body}
            // TextArea del template a veces manda string, a veces event.
            onChange={(v: any) =>
              setBody(typeof v === "string" ? v : (v?.target as HTMLTextAreaElement)?.value ?? "")
            }
          />
          {!canPublish ? (
            <p className="mt-1.5 text-xs text-gray-500 dark:text-gray-400">
              El mensaje no puede estar vacío.
            </p>
          ) : null}
        </div>
      </div>
    </div>
  );
}
